import ModalMessage from "../../../pages/messages/ModalMessage"
// const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)

class AjoutApropos extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
          files_preview:"",
          files_name:{},
          multimedia:"",
          show:false,
          success:true,
          boutton:"OK",
          message:"A propos ajouté avec succès !!",
          titre:"Message ajout de A propos"
        }
    }

  componentDidMount() {

  }

  handleRetour=()=>{
    this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
  }


  handleAddPropos=()=>{

    
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "titre": $("#titreapropos").val(),
      "text": $("#textapropos").val(),
      "urlVideo":$("#urlvideoapropos").val(),
      "telephone": $("#telephone").val(),
      "email": $("#email").val(),
      "adresse":$("#adresse").val(),
  
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"propos/add",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
     

    try{
      if(data.id){
       this.setState({show:true,success:true});
      }else{
       this.setState({show:true,success:false});
      }
   }catch(err){
     this.setState({show:true,success:false});
   }
  
   }
   
  );

  }
 
  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
    }else{
      this.setState({show:false})
    }
    
  }
 
  render() {

  // const { pick } = this.state
  // const { selectedGroup } = this.state
  // const { selectedLangue } = this.state
  // const animatedComponents = makeAnimated();

  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}

             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Ajout à propos</h1>
                  {/* </CCardHeader> */}


                  <CardBody>
                    
                    <Row  className="mt-3"> 
                      <Col md="12">
                        <label>Titre à propos</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="titreapropos"
                          placeholder="Titre à propos"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-3">
                      <Col md="12">
                        <label>Url vidéo à propos</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="urlvideoapropos"
                          placeholder="Url vidéo à propos"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-3">
                      <Col md="12">
                        <label>Text à propos</label>
                        <textarea  className="form-control" id="textapropos" placeholder="Text à propos" />
                      </Col>
                    </Row>


                    <Row className="mb-5"> 
                      <Col md="4" className="mt-3">
                        <label>Téléphone</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="telephone"
                          placeholder="Téléphone"
                        />
                      </Col>
                      <Col md="4" className="mt-3">
                        <label>Email</label>
                        <Input
                          className="form-control"
                          type="email"
                          id="email"
                          placeholder="Email"
                        />
                      </Col>
                      <Col md="4" className="mt-3">
                        <label>Adresse</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="adresse"
                          placeholder="Adresse"
                        />
                      </Col>
                    </Row>
                    
                    

                    
                    <Row className="justify-content-start mt-5">
                      <Col md="6" className="mt-1">
                        {/* <Button id="initial"  onClick={this.handleAddPropos} color="success" block className="b-lg" >
                          Ajouter à propos
                        </Button> */}
                        <a 
                          onClick={this.handleAddPropos} 
                          class="get-started-btn4 text-white text-center a btn-block ">
                          Ajouter à propos
                        </a>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                      <Col md="6" className="mt-1">
                        {/* <Button  onClick={this.handleRetour} color="secondary" block className="b-lg">
                          Annuler
                        </Button> */}
                        <a 
                          onClick={this.handleRetour}
                          class="get-started-btn5 text-white text-center a btn-block ">
                          Annuler
                        </a>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default AjoutApropos